@import url("https://fonts.googleapis.com/css2?family=Didact+Gothic&family=Quicksand:wght@500&display=swap");
* {
  box-sizing: border-box;
}
html {
  font-size: 100vmax / 1600 * 100;
  @media (max-width: 992px) {
    font-size: 20px;
  }
}
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #222;
  /* background-image: linear-gradient(45deg, #dfe0fb, #fef6bf); */
  font-size: 0.3rem;
  font-family: "Quicksand", sans-serif;
}

@media screen and (min-width: 300px) {
  *::-webkit-scrollbar {
    display: none;
  }

  /* Track */
  *::-webkit-scrollbar-track {
    display: none;
  }

  /* Handle */
  *::-webkit-scrollbar-thumb {
    display: none;
  }
}
@media screen and (min-width: 1000px) {
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

li {
  margin: 0 0.5rem;
}

a {
  text-decoration: none;
  color: inherit;
}

.main-ctn {
  width: 100vw;
  min-height: 100vh;
  height: fit-content;
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  animation: displayIt 2s forwards 5.5s;
  opacity: 0;
}

.canSeeMainCtn {
  animation: displayIt 1s forwards 1.5s;
  display: flex;
  opacity: 0;
}

@keyframes displayIt {
  to {
    opacity: 1;
  }
}

.first-slide {
  height: 100vh;
  width: 100vw;
  background-size: cover;
}

.intro-text-ctn {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.intro-text-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.65rem;
  font-weight: 500;
  letter-spacing: 4px;
  text-transform: uppercase;
  color: #555;
  text-align: center;
  margin-bottom: 0.5rem;
  user-select: none;
  max-width: 45vw;
  width: fit-content;
  font-family: "Didact Gothic", sans-serif;
  height: auto;
  margin: 0;
}
.intro-text-subtitle {
  font-size: 1.1rem;
  font-weight: 500;
  letter-spacing: 2px;
  color: #555;
  text-align: center;
  user-select: none;
  text-transform: none;
}

.other-slides {
  min-height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.visible {
  display: flex;
  opacity: 0;
  animation: fadeIn 2s forwards 3s;
}

#svg {
  width: 250px;
  height: 30px;
}

svg text,
#logo-text-id {
  font-size: 1rem;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 5px;
  fill: #555;
  user-select: none;
  text-anchor: middle;
  -o-table-baseline: middle;
}

#logo-text-id {
  font-size: 0.9rem;
  letter-spacing: 4px;
  font-weight: 500;
}

.menu-text {
  font-size: 0.8rem;
  letter-spacing: 3px;
  font-weight: 400;
}

#loading-text {
  fill: transparent;
  stroke: #555;
  stroke-width: 1.5px;
  animation: animated-stroke 4.5s;
}

@keyframes animated-stroke {
  0% {
    stroke-dasharray: 0 80%;
  }
  100% {
    stroke-dasharray: 7% 0%;
    stroke-dashoffset: 0%;
  }
}

.hidden {
  visibility: hidden;
  display: none;
}

/* For nav bar */

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3rem;
  background: none;
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  z-index: 1000;
  font-size: 0.82rem;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0 15rem;
}

#navbar {
  opacity: 0; /* Start with 0 opacity */
  animation: fadeIn 3s forwards 5s; /* Use fadeIn animation to gradually increase opacity */
  visibility: visible; /* Make the navbar visible initially */
}

@keyframes fadeIn {
  to {
    opacity: 1; /* At the end of the animation, the opacity will be 1 (fully visible) */
  }
}

.loading-container {
  position: absolute;
  display: flex;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 1;
  animation: fadeOut 2s forwards 4.5s;
}

.hideLiodingPage {
  opacity: 0;
}

.notHideLiodingPage {
  opacity: 1;
}

@keyframes fadeOut {
  to {
    opacity: 0;
  }
}

/* For particles */

#svg-particles-mouse {
  height: 100vh;
  width: 100vw;
}
rect {
  fill: none;
  pointer-events: all;
}

circle {
  fill: none;
  stroke-width: 1px;
}


body {
  background-color: #f4f4f4;
  transition: background-color 1.5s ease-in-out;
}

/* panel styles */
.panel {
  /* min height incase content is higher than window height */
  min-height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  /* outline: 10px solid hotpink; */
  /* turn above on to see the edge of panels */
}

/* colours */
.color-violet {
  background-color: #7A4EAB;
}
.color-indigo {
  background-color: #4332CF;
}
.color-blue {
  background-color: #2F8FED;
}
.color-green {
  background-color: #4DCF42;
}
.color-yellow {
  background-color: #FAEB33;
}
.color-orange {
  background-color: #F19031;
}
.color-red {
  background-color: #F2293A;
}
.color-new {
  background-color: #555;
}

