* {
  margin: 0;
  padding: 0;
}

.wrapper {
  height: 100vh;
  width: 100vw;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.circle {
  height: 60px;
  width: 60px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 50px;
  position: absolute;
  animation: animate 4s 0s linear infinite;
}

@keyframes animate {
  0% {
    transform: translate(0, 0) scale(0);
    opacity: 1;
  }
  100% {
    transform: translate(var(--transX), var(--transY)) scale(1.3);
    opacity: 0;
  }
}
